#nav {
    display: flex;
    height: 100%;
}

#links {
    display: flex;
    flex-flow: column nowrap;
    text-align: center;
    justify-content: space-between;

    position: relative;
    top: 10%;
    left: 20%;
    height: 50%;
}

#links > * {
    color: white;
    text-decoration: none;
    text-shadow: 0px 0px 5px black;
}

#nav button {
    border: none;
    border-radius: 20px;
    background: darkblue;
    padding: 10px;
}