* > button {
    margin: 5px 5px;
    padding: 8px;
    
    border: none;
    border-radius: 15px;
    
    color: white;
    background: blue;
}

#app {
    display: flex;
    flex-direction: row;
    height: 100vh;
}

#app #logo {
    z-index: 0;
    
    font-size: 2em;
    color: blue;

    position: fixed;
    top: 10px;
    right: 20%;
}

#app main {
    z-index: 1;

    display: flex;
    flex-flow: row wrap;
    justify-content: center;
    align-items: flex-start;
    
    width: 100%;
    height: 100%;
    min-width: 500px;
    overflow:auto;
}

#app main::-webkit-scrollbar {
    display: none;
}

#background {
    z-index: -1;

    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;

    width: 100vw;
    height: 100vh;
    
    background: linear-gradient(70deg, blue 0 50%, white 50% 100%);
}

#app #nav {
    z-index: 2;
    min-width: 200px;
    height: 100%;
    overflow:stretch;
}

#foot {
    display: flex;
    width: 100%;
    height: 50px;
}

#user-info {
    position: absolute;
    text-align: right;
    bottom: 5px;
    right: 5px;
}

#welcome {
    position: absolute;
    top: 40%;
    right: 10%;
}

#welcome > h1 {
    text-align: center;
    
    padding: 5px;

    font-size: 2em;
    color: blue;
}